/* roboto-100 - latin */

@font-face {
  font-weight: 100;
  font-family: "Roboto";
  font-style: normal;
  font-display: swap;
  src: local(""), url("../fonts/roboto-v27-latin-100.woff2") format("woff2"),
    url("../fonts/roboto-v27-latin-100.woff") format("woff"); /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */
}

