.video {
  padding-top: 90px;
}

.video__container {
  display: flex;
  flex-direction: column;
}

.video__text-row {
  display: flex;
  justify-content: space-between;
}

.video__title {
  padding-bottom: 0;
}

.video__player {
  order: -1;
  margin-bottom: 40px;
}

.video__text {
  max-width: 720px;
}
